"use client";

import { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { CSSProperties } from 'react';
import { 
  CheckCircle, 
  TrendingUp, 
  Zap
} from 'lucide-react';

export default function Stats() {
  const [isMounted, setIsMounted] = useState(false);
  
  useEffect(() => {
    setIsMounted(true);
  }, []);

  const sectionStyle: CSSProperties = {
    backgroundColor: '#000',
    padding: '6rem 1.5rem',
    position: 'relative',
    overflow: 'hidden',
    marginTop: '-1px'
  };

  const containerStyle: CSSProperties = {
    maxWidth: '1280px',
    margin: '0 auto',
    position: 'relative',
    zIndex: 1
  };

  const statsGridStyle: CSSProperties = {
    display: 'grid',
    gridTemplateColumns: 'repeat(auto-fit, minmax(250px, 1fr))',
    gap: '4rem'
  };

  const statItemStyle: CSSProperties = {
    textAlign: 'center'
  };

  const numberStyle: CSSProperties = {
    fontSize: 'clamp(3rem, 6vw, 4.5rem)',
    fontWeight: 700,
    marginBottom: '1rem',
    fontFamily: 'var(--font-display)',
    letterSpacing: '0.02em',
    background: 'linear-gradient(45deg, var(--primary), #fff)',
    backgroundSize: '200% 200%',
    backgroundClip: 'text',
    WebkitBackgroundClip: 'text',
    WebkitTextFillColor: 'transparent',
    lineHeight: 1,
    animation: 'gradientText 5s ease infinite'
  };

  const labelStyle: CSSProperties = {
    fontSize: '1rem',
    fontWeight: 300,
    textTransform: 'uppercase',
    letterSpacing: '0.15em',
    color: 'rgba(255, 255, 255, 0.8)',
    position: 'relative'
  };

  const decorStyle: CSSProperties = {
    position: 'absolute',
    width: '100%',
    height: '200px',
    background: 'linear-gradient(180deg, rgba(var(--primary-rgb), 0.03) 0%, transparent 100%)',
    top: 0,
    left: 0
  };

  // 3D animated cube background
  const cubeBackgroundStyle: CSSProperties = {
    position: 'absolute',
    top: '10%',
    left: '5%',
    width: '90%',
    height: '80%',
    opacity: 0.05,
    background: 'url(/grid-pattern.svg), linear-gradient(to right, transparent, rgba(var(--primary-rgb), 0.05) 50%, transparent 100%)',
    zIndex: 0
  };

  // Stats data
  const stats = [
    { value: '99.9', label: 'Uptime Percentage', unit: '%' },
    { value: '500+', label: 'Enterprise Clients', unit: '' },
    { value: '24/7', label: 'Support Coverage', unit: '' },
    { value: '8', label: 'Years of Excellence', unit: '+' }
  ];

  // For counting animation
  const counterVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: { 
      opacity: 1, 
      y: 0,
      transition: {
        duration: 0.5
      }
    }
  };

  const features = [
    {
      id: 1,
      icon: <CheckCircle className="w-8 h-8 text-primary" />,
      title: "Identify Pain Points",
      description: "Uncover and fix inefficiencies that are holding you back, streamlining your path to growth."
    },
    {
      id: 2,
      icon: <Zap className="w-8 h-8 text-primary" />,
      title: "Eliminate Bottlenecks",
      description: "Streamline your workflows for smooth, efficient, and hassle-free operations."
    },
    {
      id: 3,
      icon: <TrendingUp className="w-8 h-8 text-primary" />,
      title: "High ROI & Scalability",
      description: "Grow confidently with flexible solutions that expand alongside your business."
    }
  ];

  return (
    <section style={sectionStyle}>
      <div style={decorStyle}></div>
      <motion.div 
        style={cubeBackgroundStyle}
        animate={{
          backgroundPosition: ['0% 0%', '100% 100%'],
        }}
        transition={{
          duration: 20,
          repeat: Infinity,
          repeatType: 'reverse'
        }}
      ></motion.div>
      
      {/* Add diagonal connector between sections */}
      <div style={{
        position: 'absolute',
        top: '-50px',
        left: 0,
        width: '100%',
        height: '100px',
        backgroundColor: '#000',
        transform: 'skewY(-1.5deg)',
        transformOrigin: 'top left',
        zIndex: 0
      }}></div>
      
      <div style={containerStyle}>
        <motion.h2
          style={{
            fontSize: 'clamp(2rem, 5vw, 3rem)',
            fontWeight: 700,
            marginBottom: '4rem',
            fontFamily: 'var(--font-display)',
            letterSpacing: '0.02em',
            textTransform: 'uppercase',
            textAlign: 'center',
            background: 'linear-gradient(45deg, #fff, var(--primary))',
            backgroundSize: '200% 200%',
            backgroundClip: 'text',
            WebkitBackgroundClip: 'text',
            WebkitTextFillColor: 'transparent',
            animation: 'gradientText 5s ease infinite'
          }}
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
          viewport={{ once: true, amount: 0.3 }}
        >
          Our Impact By Numbers
        </motion.h2>
        
        <div style={statsGridStyle}>
          {stats.map((stat, index) => (
            <motion.div
              key={index}
              style={statItemStyle}
              initial="hidden"
              whileInView="visible"
              viewport={{ once: true, amount: 0.3 }}
              variants={{
                hidden: { opacity: 0, y: 30 },
                visible: { 
                  opacity: 1, 
                  y: 0,
                  transition: {
                    duration: 0.5,
                    delay: index * 0.1
                  }
                }
              }}
            >
              <motion.div
                style={{
                  background: 'rgba(255, 255, 255, 0.02)',
                  borderRadius: '8px',
                  padding: '2.5rem',
                  height: '100%',
                  border: '1px solid rgba(255, 255, 255, 0.05)',
                  display: 'flex',
                  flexDirection: 'column',
                  justifyContent: 'center',
                  alignItems: 'center'
                }}
                whileHover={{
                  y: -10,
                  boxShadow: '0 20px 40px rgba(0, 0, 0, 0.2)',
                  background: 'rgba(255, 255, 255, 0.04)',
                  borderColor: 'rgba(var(--primary-rgb), 0.3)'
                }}
                transition={{ duration: 0.3 }}
              >
                <motion.h3 
                  style={numberStyle}
                  variants={counterVariants}
                >
                  {isMounted ? (
                    <CountUp
                      value={parseFloat(stat.value)}
                      unit={stat.unit}
                    />
                  ) : (
                    <span>{stat.value}</span>
                  )}
                </motion.h3>
                <motion.p 
                  style={labelStyle}
                  variants={counterVariants}
                >
                  {stat.label}
                </motion.p>
              </motion.div>
            </motion.div>
          ))}
        </div>
      </div>

      <style jsx global>{`
        @keyframes gradientText {
          0% {
            background-position: 0% 50%;
          }
          50% {
            background-position: 100% 50%;
          }
          100% {
            background-position: 0% 50%;
          }
        }
      `}</style>
    </section>
  );
}

// Simple counter component
function CountUp({ value, unit }: { value: number, unit: string }) {
  const [count, setCount] = useState(0);
  
  useEffect(() => {
    let start = 0;
    const end = value;
    const duration = 2000;
    let startTimestamp = 0;
    
    const step = (timestamp: number) => {
      if (!startTimestamp) startTimestamp = timestamp;
      const progress = Math.min((timestamp - startTimestamp) / duration, 1);
      const current = Math.floor(progress * (end - start) + start);
      
      setCount(current);
      
      if (progress < 1) {
        window.requestAnimationFrame(step);
      } else {
        setCount(value);
      }
    };
    
    window.requestAnimationFrame(step);
  }, [value]);
  
  return (
    <span>{!isNaN(count) ? count : value}{unit}</span>
  );
} 